/**
 * PAGE.
 */
var Router = (function() {

  'use strict';

  /**
   * MODULES.
   */
  var config = require('../config.js');
  var login = require('../pages').login;
  var logout = require('../pages').logout;


  /**
   * VARIABLES.
   */
  var apiUsersUrl = ['', config.client.api.path, config.client.api.version, 'users'].join('/');
  var pageLookup = {};
  [login, logout].forEach(function(page) {
    pageLookup[page.path] = page;
  });


  /**
   * FUNCTIONS.
   */
  function Router() {

    $('a').click(linkClick);

  }

  function linkClick(e) {

    e.preventDefault();

    if (!pageLookup.hasOwnProperty(this.pathname)) {
      return console.error('Cannot find page');
    }

    var page = pageLookup[this.pathname];
    
    $.ajax({
      type: 'get',
      url: this.pathname,
      success: page.onSuccess,
      error: page.onError
    });

  }

  return Router;

}) ();

/**
 * EXPORTS.
 */
module.exports = Router;