/**
 * PAGE.
 */
var Login = (function() {

  'use strict';

  /**
   * MODULES.
   */
  var config = require('../config.js');


  /**
   * VARIABLES.
   */
  var apiUsersUrl = ['', config.client.api.path, config.client.api.version, 'users'].join('/');


  /**
   * FUNCTIONS.
   */
  function Login() {

    $('.page-login').find('.login').find('form').on('submit', loginUser);
    $('.page-login').find('.register').find('form').on('submit', registerUser);

  }

  function registerUser(e) {

    var user = {
      name: this.elements.name.value,
      password: this.elements.password.value,
      passwordwdh: this.elements.passwordwdh.value,
      email: this.elements.email.value,
    };

    $.ajax({
      type: 'post',
      url: apiUsersUrl,
      data: user,
      dataType: 'json',
      success: registerUserSuccess,
      error: registerUserError
    });

    e.preventDefault();

    return false;

  }

  function registerUserSuccess(response) {

    swal({
      title: 'Registrierung erfolgreich!',
      text: 'SchlieÃŸe dich einer Liga an und werde Manager.',
      type: 'success'
    }, function onRegistration() {
      location.href = '/';
    });

  }

  function registerUserError(err) {

    swal({
      title: 'Registrierung fehlgeschlagen!',
      text: 'Bei den Anmeldedaten stimmt etwas nicht.',
      type: 'error'
    });

  }

  function loginUser(e) {

    var user = {
      email: this.elements.email.value,
      password: this.elements.password.value
    };

    $.ajax({
      type: 'post',
      url: '/login',
      data: user,
      dataType: 'json',
      success: loginUserSuccess,
      error: loginUserError
    });

    e.preventDefault();

    return false;

  }

  function loginUserSuccess(response) {

    location.href = '/';

  }

  function loginUserError(err) {
    
    swal({
      title: 'Oops...',
      text: err.responseJSON.message,
      type: 'error'
    });

  }

  return Login;

}) ();

/**
 * EXPORTS.
 */
module.exports = Login;